import React , { useState } from "react";
import DatePicker from "react-datepicker";
import "./Datepicker.css";

function Airbnbdatepicker(props) {
const [selectDate, setSelectedDate] = useState(null)
return (

<div className="bg-white w-[800px] h-16 rounded-full">
<div className="px-7 absolute py-2 font-semibold text-sm ml-60 z-10 ">Check in </div>
<div className=" bg-white w-60 h-16 rounded-full absolute ml-60 px-7 hover:shadow-xl flex items-center pt-5">
    <DatePicker selected={selectDate} onChange={(date) => setSelectedDate(date)} />
    
</div>

<div className="px-7 absolute py-2 font-semibold text-sm ml-[480px] z-10 ">Check out </div>
<div className=" bg-white w-60 h-16 rounded-full absolute ml-[480px] px-7 hover:shadow-xl flex items-center pt-5">
    <DatePicker selected={selectDate} onChange={(date) => setSelectedDate(date)} />
    
</div>
    
    <div className="w-full ">
    <div className="px-7 absolute py-2 font-semibold text-sm ">Location </div>
    <input
    className="w-60 hover:shadow-xl rounded-full px-7 h-16 text-base pt-3 focus:outline-none"
    input type="text"
    placeholder="Where are you going?"
        />
</div>

</div>
);
}

export default Airbnbdatepicker;